"use strict";
const development_core_1 = require('development-core');
const BaseLoader_1 = require('./BaseLoader');
const contextDefine_1 = require('../utils/contextDefine');
const chalk = require('chalk');
class ModuleLoader extends BaseLoader_1.BaseLoader {
    constructor(option, env, factory) {
        super(option, env, factory);
    }
    getContextDefine() {
        return new Promise((resolve, reject) => {
            let loader = this.option.loader;
            if (loader) {
                if (loader.contextDefine) {
                    resolve(loader.contextDefine);
                }
                else if (loader.taskDefine) {
                    resolve(development_core_1.taskDefine2Context(loader.taskDefine));
                }
                else {
                    let mdl = this.getConfigModule();
                    development_core_1.findTaskDefineInModule(mdl)
                        .then(def => {
                        if (def) {
                            resolve(def);
                        }
                        else {
                            resolve(contextDefine_1.default(this.getTaskModule()));
                        }
                    })
                        .catch(err => {
                        console.error(chalk.red(err));
                        resolve(contextDefine_1.default(this.getTaskModule()));
                    });
                }
            }
            else {
                reject('loader not found.');
            }
        });
    }
}
exports.ModuleLoader = ModuleLoader;
